import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import { BsPlusCircle, BsThreeDotsVertical } from 'react-icons/bs';
import Modal from 'react-bootstrap/Modal';
import { minInvestment } from 'views/mobileView/components/atoms/MinInvestment'
import { AiOutlineDown } from 'react-icons/ai';
import { FaLock, FaUnlock } from 'react-icons/fa';

const MintboxAllocation = (props) => {

    const [selectedETFs, setSelectedETFs] = React.useState([]);

    const [showMainModal, setShowMainModal] = React.useState(false)
    const [showFundModal, setShowFundModal] = React.useState(false)
    const [selectedModalFund, setSelectedModalFund] = React.useState()

    React.useEffect(async () => {
        await getSelectedETFs();
    }, []);

    React.useEffect(() => {
        saveData(selectedETFs)
        console.log(selectedETFs)
    }, [selectedETFs]);

    const getSelectedETFs = async () => {
        try {
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.selectedETFs) {
                    console.log("SelectedETFS", u.selectedETFs)
                    setSelectedETFs(u.selectedETFs)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };
    const saveData = async (arr) => {
        const dataFinal = {
            selectedETFs: arr
        };
        await db.get(db.model.createMintbox).then((u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            db.set(db.model.createMintbox,JSON.stringify(u));
        })
    }

    const areWeightsUnequal = () => {
        const count = selectedETFs.length
        return selectedETFs.some((fund, i) => {
            if (i === 0) {
                if (fund.allocation !== Math.floor(100 / count) + 100 % count) {
                    return true
                }
            }
            else {
                if (fund.allocation !== Math.floor(100 / count)) {
                    return true
                }
            }
        })
    }
    const equalizeAllWeights = () => {
        const count = selectedETFs.length
        const newArr = selectedETFs.map((fund, i) => {
            if (i === 0) { return { ...fund, isLocked: false, allocation: Math.floor(100 / count) + 100 % count } }
            else return { ...fund, isLocked: false, allocation: Math.floor(100 / count) }
        })
        setSelectedETFs(newArr)
    }

    const equalizeUnlockedWeights = (arr) => {
        console.log("equalize")
        const unlockedETFsCount = arr.filter(f => !f.isLocked).length;
        console.log("unlockedEtfCount", unlockedETFsCount)
        if (unlockedETFsCount === 0) {
            toast.error("Unlock some ETFs to readjust weights");
            return;
        }
        const lockedWeight = arr.reduce((sum, f) => {
            if (f.isLocked) {
                return sum + f.allocation
            }
            return sum
        }, 0)
        console.log("lockedWeight", lockedWeight)
        const remainingWeight = 100 - lockedWeight;
        if (remainingWeight < unlockedETFsCount) {
            toast.error("Unlock some ETFs to readjust weights");
            return;
        }
        const firstNotLockedIndex = arr.findIndex((f) => !f.isLocked)
        console.log('unlockedETFsCount:', unlockedETFsCount)
        console.log('remainingWeight:', remainingWeight)
        const newArr = arr.map((fund, i) => {
            if (fund.isLocked) {
                return fund;
            }
            if (i === firstNotLockedIndex) { return { ...fund, allocation: Math.floor(remainingWeight / unlockedETFsCount) + remainingWeight % unlockedETFsCount } }
            else return { ...fund, allocation: Math.floor(remainingWeight / unlockedETFsCount) }
        })
        console.log("newArr", newArr);
        setSelectedETFs(newArr)
    }

    const assignWeights = (index, value = 0) => {
        console.log("assign", index, value)
        if (!value) {
            value = 0
        }
        let newArr = selectedETFs.map((fund, i) => {
            if (i === index) {
                return { ...fund, allocation: value, isLocked: true }
            }
            else return fund
        })
        equalizeUnlockedWeights(newArr)
    }

    const checkWeights = () => {
        if (selectedETFs.length < 2) return false;
        if (selectedETFs.some(f => f.allocation === 0)) return false;
        if (selectedETFs.reduce((sum, f) => sum + f.allocation) !== 100, 0) return false;
        return true;
    }

    const onDelete = (id) => {
        equalizeUnlockedWeights(selectedETFs.filter(fund => fund.Symbol !== id));
    }

    const onReplace = (fund) => {
        props.history.push({
            pathname: '/CreateMintbox/replace-ETF',
            state: { fundToReplace: fund }
        })
    }

    const onLock = (id) => {
        console.log("onlock", selectedETFs)
        const newArr = selectedETFs.map(fund => {
            return fund.Symbol === id ? { ...fund, isLocked: !fund.isLocked } : fund
        })
        console.log("after lock", newArr)
        setSelectedETFs(newArr)
    }


    const Footer = () => {
        const onNextPress = () => {
            if (checkWeights()) {
                props.history.push({ pathname: '/CreateMintbox/rebalancing-strategy', state: { minInv: minInvestment(selectedETFs) } })
            }
        }
        return (
            <>
                <div className="bodyTextLight" style={{ position: "fixed", bottom: '70px', width: "100%", textAlign: "center", borderTop: "1px solid " + cssVar.getPropertyValue('--light'), padding: "2% 4%", backgroundColor: cssVar.getPropertyValue('--white') }}>
                    Min investment <span className='header2dark'>₹ {Number(minInvestment(selectedETFs, 1)).toFixed(2)}</span>
                </div>
                <div className='footer'>
                    <div className='bodyTextDark' style={{ flex: 1, paddingRight: '2%' }}>
                        Choose rebalancing frequency next
                    </div>
                    <div style={{ flex: 1 }}>
                        <button
                            className='solidButton'
                            style={{ opacity: checkWeights() ? 1 : 0.4 }}
                            onClick={() => { onNextPress() }}
                        >
                            Confirm weightages
                        </button>
                    </div>
                </div>
            </>
        )
    }
    const MainModal = () => {

        return (
            <Modal
                className="bottom-drawer"
                show={showMainModal}
                onHide={() => setShowMainModal(false)}
                keyboard={false}
            >
                <Modal.Body>
                    <div className="radio-item" style={{ display: 'block', opacity: areWeightsUnequal()?1:0.4 }}>
                        <input type="radio" name="equalize" value="equalize" id="equalize" disabled={!areWeightsUnequal()} defaultChecked={areWeightsUnequal()}/>
                        <label className="bodyTextDark radio-label" htmlFor="equalize">Allocate equal weightages to all</label>
                    </div>
                    {
                        areWeightsUnequal()?
                        <></>
                        :
                        <div className='bodyTextDark' style={{marginTop:'4%'}}>Weights are already equal</div>
                    }
                </Modal.Body>
                <Modal.Footer>
                    <button className="outlineButton" onClick={() => setShowMainModal(false)}>
                        Close
                    </button>
                    {
                        areWeightsUnequal()?
                            <button className="solidButton" onClick={() => {
                                equalizeAllWeights();
                                setShowMainModal(false);
                            }}>
                                Confirm
                            </button>
                            :
                            <></>
                    }
                </Modal.Footer>
            </Modal>
        )
    }
    const FundModal = () => {
        const [selectedOption, setSelectedOption] = React.useState()

        const closeModal = () => {
            setSelectedModalFund({});
            setShowFundModal(false);
        }
        console.log(selectedModalFund)
        const onConfirm = () => {
            switch (selectedOption) {
                case 'replace': {
                    onReplace(selectedModalFund);
                    setShowFundModal(false);
                    break;
                }
                case 'remove': {
                    onDelete(selectedModalFund.Symbol);
                    setShowFundModal(false);
                    break;
                }
                default: closeModal()
            }
        }
        return (
            <Modal
                className="bottom-drawer"
                show={showFundModal}
                onHide={() => closeModal()}
                keyboard={false}
            >
                <Modal.Body>
                    <div className='header1dark'>
                        {selectedModalFund?.ETFName || ''}
                    </div>
                    <div className='smallTextLight'>
                        Select action
                    </div>
                    <div className="radio-item" style={{ marginTop: "6%" }}>
                        <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="replace" id="replace" />
                        <label className="bodyTextDark radio-label" htmlFor="replace">Replace this ETF</label>
                    </div>
                    <div className="radio-item" style={{ marginTop: "6%" }}>
                        <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="remove" id="remove" />
                        <label className="bodyTextDark radio-label" htmlFor="remove" style={{ color: cssVar.getPropertyValue('--peach') }}>Remove ETF from mintbox </label>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <button className="outlineButton" onClick={() => closeModal()}>
                        Close
                    </button>
                    <button className="solidButton" onClick={() => onConfirm()}>
                        Confirm
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    return (
        <div className='mainContainer'>
            <MainHeader
                header="Build your own mintbox"
                navigation={props.history}
            />
            <div className="dataContainer px-0" style={{ paddingBottom: "140px", paddingTop:"2%" }}>
                <div className="header2dark" style={{ margin: "4%", display: "flex", gap: "4%" }}>
                    <div style={{ flex: 1 }}>ETFs in your mintbox</div>
                    <div className='secondaryGreen' onClick={() => setShowMainModal(true)}>
                        Weightages <AiOutlineDown size={14} />
                    </div>

                </div>
                {
                    selectedETFs.map((fund, index) => {
                        return (
                            <div style={{ padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }} key={fund.Symbol}>
                                <div style={{ display: "flex", gap: "5%", alignItems: 'center' }}>
                                    <BsThreeDotsVertical className='secondaryGreen' size={26} style={{ marginLeft: "-10px" }}
                                        onClick={() => {
                                            setSelectedModalFund(fund)
                                            setShowFundModal(true)
                                        }} />
                                    <div style={{ flex: "1" }}>
                                        <div className='header2dark'>{fund.ETFName}</div>
                                        <div className='smallTextLight' style={{marginTop:'1%'}}>NAV ₹ {Number(fund.NAV).toFixed(2)}</div>
                                    </div>
                                    {
                                        fund.isLocked ?
                                            <FaLock size={16} className='secondaryGreen' onClick={() => onLock(fund.Symbol)} />
                                            :
                                            <FaUnlock size={16} style={{ color: cssVar.getPropertyValue('--light') }} onClick={() => onLock(fund.Symbol)} />
                                    }
                                    <input type="tel" maxLength={2} value={fund.allocation} style={{ width: "60px", height: "37px", textAlign: "center", border: "1px solid " + cssVar.getPropertyValue('--light'), outline: "none", borderRadius: "4px", background: cssVar.getPropertyValue('--lightest') }}
                                        onChange={e => assignWeights(index, parseInt(e.target.value))}
                                    />
                                </div>
                            </div>
                        )
                    })
                }
                <div className='header3dark secondaryGreen' style={{ padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light'), textAlign: 'center' }} onClick={() => props.history.goBack()}>
                    Add more ETFs<BsPlusCircle size={16} style={{ background: cssVar.getPropertyValue('--conservativeRisk'), borderRadius: "50%", marginLeft: "2%" }} />
                </div>
                {selectedETFs.length === 1 && <div className='header2dark' style={{ margin: "4%" }}>Add at least <b>one more</b> ETF to build your own mintbox</div>}
            </div>

            <Footer />
            <MainModal />
            <FundModal />
        </div>
    )
}

export default MintboxAllocation
